.highlight {
  display: flex;
  padding: 20px;
  width: 740px;
  border-radius: 10px;
  -webkit-transition: background-color 0.2s; }

.highlight:hover {
  background-color: #383940; }

.article-list {
  float: left; }

.highlight .raiting {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 112px;
  font-size: 20px; }

.highlight .raiting img {
  width: 25px;
  hight: 25px; }

.content {
  text-align: center;
  width: 300px;
  line-height: 50px;
  margin: 0 auto; }

.article__title {
  width: 300px;
  height: 60px; }

.article__comment {
  cursor: pointer;
  width: 260px;
  height: 50px; }

.article__comment span {
  width: 120px;
  height: 48px;
  float: left;
  transition: background-color 250ms ease 0ms, color 0s ease; }

.article__comment span:hover {
  background-color: rgba(251, 251, 251, 0.8);
  color: #27282d; }

.article__title a span {
  display: inline-block;
  text-align: left;
  width: 300px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #2955BC;
  font-size: 22px;
  text-decoration: none; }

.thumbnail img {
  cursor: pointer;
  width: 150px; }

.preview {
  padding-top: 10px;
  width: 740px;
  background-color: #2a2b30; }

.comment {
  padding-top: 10px;
  width: 740px;
  background-color: #2A2B30; }

.comment_inner {
  width: 700px;
  margin: 0 auto; }
